@import '~antd/dist/antd.dark.less';

::-webkit-scrollbar { /* chrome based */
    width: 0px;  /*  scrollbar'a */
    background: transparent;  /*  */
}

html {
    -ms-overflow-style: none;  /* IE 10+ */
    scrollbar-width: none; /* Firefox */
}

.main_content {
    background-color: #323541;
    width: 100%;
    height: calc(100vh - 144px);
    padding-bottom: 0;
    overflow: auto;
}

.content-layout {
    padding: 0 24px 24px;
    margin-top: 20px;
}

.content-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.content-layout-padding {
    padding: 0 24px 24px;
}

.dot{
    display: block;
    width: 10px;
    height: 10px;
    margin-right: 15px;
    border-radius: 50%;
    background-color: rgb(71, 71, 71);
}

.Breadcrumb-custome {
    margin: 16px 0;

& > ol{
    align-items: center;
}

}

.auth {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-image: url("data:image/svg+xml,%0A%3Csvg viewBox='0 0 1920 1080' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_1_4)'%3E%3Cline x1='191.5' x2='191.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='383.5' x2='383.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='575.5' x2='575.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='767.5' x2='767.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='960.5' x2='960.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='1151.5' x2='1151.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='1343.5' x2='1343.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='1535.5' x2='1535.5' y2='1080' stroke='%230000001c'/%3E%3Cline x1='1727.5' x2='1727.5' y2='1080' stroke='%230000001c'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_1_4'%3E%3Crect width='1920' height='1080' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");
        background-size: 100%;
        z-index: -1;
    }

    &__wrapper {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        background-color: #323541;
        padding: 15px;
        border-radius: 16px;
        max-width: 610px;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);

        @media (min-width: 768px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media (min-width: 400px) {
            padding: 32px;
        }
    }

    &__logo {
        width: 100%;
        margin: 0 auto;
    }

    &__title {
        text-align: center;
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 20px;
    }

    &__description {
        text-align: center;
        margin-bottom: 24px;
    }

    &__form-item {
        margin-bottom: 15px !important;

        @media (min-width: 400px) {
            margin-bottom: 24px !important;
        }
    }

    &__button {
        margin-top: 16px;
    }
}


// ======= ANTD THEME =======
body {
    background-color: #383B46;
}

.ant-layout-sider-trigger {
    background-color: #323541;
    position: absolute;
    height: 66px;
    width: 14px !important;
    right: -20px;
    top: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #cfcfcf66;
    transform: translate(-50%);
    clip-path: polygon(0px 0px, 100% 10px, 100% calc(100% - 10px), calc(100% - 16px) 100%, 0 100%, 0 0px);
}

.ant-layout {
    background-color: #383B46;
}

.ant-layout-sider {
    background: #323541;
}


.ant-menu.ant-menu-dark {
    background: #323541;
}

.ant-layout-header {
    background: #323541;
    box-shadow: inset 0px -1px 0px #56565F;
}

.ant-layout-content {

    &>.ant-tabs {
        height: 100%;
    }
}

.ant-menu-item-divider {
    border-color: #393b46;
}

.ant-modal-content {
    background-color: #323541;
}

.ant-dropdown-menu-dark {
    background: #393b46;

}


// TABS
.ant-tabs>.ant-tabs-nav .ant-tabs-nav-wrap,
.ant-tabs>div>.ant-tabs-nav .ant-tabs-nav-wrap {
    padding: 0px 20px 20px 0;
    background-color: #323541;
}



.ant-tabs-nav:has(.ant-tabs-extra-content) {
    .ant-tabs-nav-wrap {
        
        margin-bottom: 10px !important;
    }
}

.ant-tabs-card>.ant-tabs-nav .ant-tabs-tab,
.ant-tabs-card>div>.ant-tabs-nav .ant-tabs-tab {
    background: transparent;
    margin-top: 20px;
}

.ant-tabs-card>.ant-tabs-nav .ant-tabs-tab-active,
.ant-tabs-card>div>.ant-tabs-nav .ant-tabs-tab-active {
    background: #F59337;
}

.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
    color: #ffffff;
}

.ant-tabs-card>.ant-tabs-nav .ant-tabs-tab,
.ant-tabs-card>div>.ant-tabs-nav .ant-tabs-tab {
    border: none;
    border-radius: 0 20px 20px 0 !important;
}


.ant-tabs-left>.ant-tabs-content-holder,
.ant-tabs-left>div>.ant-tabs-content-holder {
    margin-left: 20px;
    padding: 20px;
    border: none;
    background-color: #323541;
}

.ant-tabs-left>.ant-tabs-content-holder>.ant-tabs-content>.ant-tabs-tabpane,
.ant-tabs-left>div>.ant-tabs-content-holder>.ant-tabs-content>.ant-tabs-tabpane {
    padding: 0;
}

.ant-tabs-tab-btn {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}
.ant-tabs-nav-operations{
    display: none !important;
}


// ./TABS

.ant-form-item {
    margin-bottom: 0;
}



// MODAL
.ant-modal-header {
    background: #323541;
}

// ./MODAL

// CARD
.ant-card-actions {
    background-color: #383B46;
    border-top: 1px solid #323541;
}

.ant-card-actions>li:not(:last-child) {
    border-right: 1px solid #323541;
}

// ./CARD

// Skeleton
.ant-skeleton-element .ant-skeleton-input-sm {
    min-width: auto;
}
// ./Skeleton

.content_full_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: inset 0px -1px 0px #56565f;
    margin-bottom: 10px;
    padding-bottom: 10px;
}


// FIELD CREATE
.field-form{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
}

.field-create{
    border: 1px solid #56565F;
}

.field-create__content{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10px;
    transition: 0.2s;

    &:hover {
        box-shadow: 0px 0px 5px 0px #F59337;
        cursor: pointer;
    }

    &:hover .fields-create__title {
        color: #F59337;
    }
}

.fields-create__title{
    text-transform: uppercase;
    margin-top: 5px;
    font-size: 12px;
    transition: 0.2s;

}
// ./FIELD CREATE

.fields-modal > .ant-modal-content > .ant-modal-body{
    padding-top: 0;
}

.fields-tab > .ant-tabs-nav > .ant-tabs-nav-wrap {
        padding: 0;
}

.ant-table-cell > .flag_tag{
    font-size: 17px;
    margin: 2px;
}

.ant-drawer-content{
    background: #323541;
}

.table_content > .ant-spin-nested-loading > .ant-spin-container > .ant-table{
    height: calc(100vh - 240px);
    overflow: auto;
    box-shadow: 0 -8px 10px -5px #333541;
}

.table_pagination{
    position: relative;
    padding: 15px 0 0 0;
    justify-content: start;
    margin: 0 !important;
    box-shadow: 0 -8px 10px -5px #333541;
    border-top: 1px solid #80808024;
    z-index: 1;
}

.ant-table{
 background: none;
}

.ant-table-tbody{
    background: #3c3e49;
}

.ant-pagination-options{
    position: absolute;
    right: 0;
}

.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
    background-color: #f59338;
}

.ant-checkbox-inner {
    border: 1px solid #5e5e5e;
}

.layout {
    padding: 0 24px 24px;
}


.ant-collapse > .ant-collapse-item.ant-collapse-no-arrow > .ant-collapse-header {
    padding: 0;
}

.ant-collapse-content {
    background-color: #2d2f3a;
}

.ant-rate{
    color: #ff9800
}

.ant-tabs-extra-content{
    width: 100%;
}

.ant-table-row-expand-icon {
    border: 1px solid #5e5e5e;
}

.button_disabled {
    opacity: .2;
    cursor: default;

}
.ant-table-row-expand-icon:focus, .ant-table-row-expand-icon:hover {
    color: inherit !important;
    border-color: #5e5e5e;
}

.rich-text_create > .ql-container > .ql-editor {
    min-height: 200px;
}

.ql-toolbar.ql-snow{
    border: 1px solid #434343;
}

