    margin-left: 10px;
    margin-right: 10px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 9px;
    color: grey;
    text-transform: uppercase;
    border-left: 1px solid #80808024;
    border-right: 1px solid #80808024;
}

.field-name-border_landing{
    padding-left: 10px;
    padding-right: 10px;
    font-size: 9px;
    color: grey;
    text-transform: uppercase;
    border-left: 1px solid #80808024;
    border-right: 1px solid #80808024;
